html {
    font-family: Helvetica;
    font-size: 12px;
    line-height: 1.2rem;
}

body {
    width: 100vw;
    overflow-x: hidden;
    overflow-y: auto;
    background-color: #C9F8B7;

}



.content-section {
    width: 95vw;
    height: 80vh;
    margin: .5rem auto 3rem auto;
    box-shadow: 2px 2px 3px lightgrey;
    overflow-x: hidden;
    overflow-y: auto;
    font-size: .9rem;
    border: solid .5px lightgrey;
    border-radius: 5px;
}


.flex-center {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    flex-wrap: wrap;
}

.wrapped {
    flex-wrap: nowrap;
}

@media only screen and (max-width: 600px) {
    .wrapped {
        flex-wrap: wrap;
    }
}