* {
  margin: 0;
  padding: 0;
  box-sizing: border-box; }

a {
  text-decoration: none; }

h1,
h2,
h3,
h4,
h5,
h6 {
  text-align: center; }

ul {
  list-style: none; }

html {
  font-family: Helvetica;
  font-size: 12px;
  line-height: 1.2rem; }

body {
  width: 100vw;
  overflow-x: hidden;
  overflow-y: auto;
  background-color: #C9F8B7; }

.content-section {
  width: 95vw;
  height: 80vh;
  margin: .5rem auto 3rem auto;
  box-shadow: 2px 2px 3px lightgrey;
  overflow-x: hidden;
  overflow-y: auto;
  font-size: .9rem;
  border: solid .5px lightgrey;
  border-radius: 5px; }

.flex-center {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  flex-wrap: wrap; }

.wrapped {
  flex-wrap: nowrap; }

@media only screen and (max-width: 600px) {
  .wrapped {
    flex-wrap: wrap; } }

.find-form {
  padding-left: 3rem; }

.form-wrapper {
  width: 100vw; }

.string-inputs input {
  width: 18ch;
  margin-left: .5rem; }

.input-wrapper {
  display: flex;
  flex: 0 0 calc(30% - .5rem);
  flex-wrap: nowrap;
  margin: .3rem;
  padding: .3rem;
  border: solid 1px lightgray;
  box-shadow: 2px 2px 3px lightgrey;
  border-radius: 5px; }

.string-inputs {
  justify-content: center;
  flex-wrap: nowrap;
  display: flex;
  align-items: center; }

.input-wrapper select {
  width: 20ch;
  height: 1.5rem; }

.input-wrapper .number-selector {
  width: 5ch; }

#levi-button,
#double-button {
  width: 20ch; }
