.find-form {
    padding-left: 3rem;
}

.form-wrapper {
    width: 100vw;
}

.string-inputs input {
    width: 18ch;
    margin-left: .5rem;
}

.input-wrapper {

    display: flex;
    flex: 0 0 calc(30% - .5rem);
    flex-wrap: nowrap;
    margin: .3rem;
    padding: .3rem;
    border: solid 1px lightgray;
    box-shadow: 2px 2px 3px lightgrey;
    border-radius: 5px;
}

.string-inputs {
    justify-content: center;
    flex-wrap: nowrap;
    display: flex;
    align-items: center;
}

.input-wrapper {
    & select {
        width: 20ch;
        height: 1.5rem;
    }

    & .number-selector {
        width: 5ch;
    }

}

#levi-button,
#double-button {
    width: 20ch;
}